"use strict";
const container = document.querySelector("#root");
const ajax = new XMLHttpRequest();
const content = document.createElement("div");
const NEWS_URL = "https://api.hnpwa.com/v0/news/1.json";
const CONTENT_URL = "https://api.hnpwa.com/v0/item/@id.json";

function getData(url) {
  // ë™ê¸°ì ìœ¼ë¡œ ì²˜ë¦¬í•  ê²½ìš° ë§ˆì§€ë§‰ ì˜µì…˜ ê°’ì„ falseë¥¼ ì£¼ë©´ ë©ë‹ˆë‹¤.
  ajax.open("GET", url, false);
  ajax.send();

  // ajax.response ë¥¼ ì‚¬ìš©í•˜ë©´ ê°’ì„ ë¶ˆëŸ¬ ì˜¬ ìˆ˜ ìˆë‹¤ JSON íƒ€ì…
  // JSONíŒŒì¼ì„ ê°ì²´í™” ì‹œì¼œì£¼ê¸° ìœ„í•´ JSON.parseë¥¼ ì‚¬ìš©!
  return JSON.parse(ajax.response);
}

const newsFeed = getData(NEWS_URL);
const ul = document.createElement("ul");

window.addEventListener("hashchange", () => {
  // substr(startIndex, length), substring(startIndex, endIndex), slice(startIndex, endIndex) ë¬¸ìì—´ ì˜ë¼ë‚´ëŠ” ë°©ë²•!
  const id = location.hash.substr(1);

  const newsContent = getData(CONTENT_URL.replace("@id", id));
  const title = document.createElement("h1");

  title.innerHTML = newsContent.title;
  content.appendChild(title);
});

// dom APIë¥¼ ìµœëŒ€í•œ ì‚¬ìš©í•˜ì§€ ì•Šê³  innerHTMLì„ ì‚¬ìš©í•´ì„œ ë§ˆí¬ì—… êµ¬ì¡°ë¥¼ í•œëˆˆì— ë³´ê¸° ì‰½ê²Œ ìˆ˜ì •
for (let i = 0; i < 10; i++) {
  const div = document.createElement("div");

  div.innerHTML = `
  <li>
    <a href="#${newsFeed[i].id}">${newsFeed[i].title} (${newsFeed[i].comments_count})</a>
  </li>
  `;

  // divëŠ” ë‹´ì•„ì£¼ëŠ” ê·¸ë¦‡ì´ì˜€ê¸°ë•Œë¬¸ì—, ulí…Œê·¸ ì•ˆì— firstElementChildë¥¼ í™œìš©í•´ì„œ liíƒœê·¸ë§Œ append í•´ì¤€ë‹¤.
  ul.appendChild(div.firstElementChild);
}

container.appendChild(ul);
container.appendChild(content);
